import React, {useState,useEffect} from 'react'
import ReactDOM from 'react-dom'
import SwiperCore, {
    FreeMode,Navigation,Thumbs
} from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import 'swiper/swiper.min.css'
import 'swiper/modules/effect-cards/effect-cards.js'
import 'swiper/modules/pagination/pagination.min.css'
import { headerIcon } from '../icon';
import InlineSVG from 'svg-inline-react';
import {collectionIcons} from '../icon'



SwiperCore.use([FreeMode,Navigation,Thumbs]);

const MiniSlider =()=>{
    
    const [objectUst, setObjectUst]=useState({})
    const [objectAlt, setObjectAlt]=useState({})
    React.useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    const hiddenThumbs=(e)=>{
        document.querySelector('.lightbox').classList.remove('hidden')
        document.querySelector('.mini-slider-button').classList.remove('hidden')
        var filter=document.getElementsByClassName("mini-slider-element active")
        Array.from(filter).map(element => (
            element.classList.remove('active')
        ))
        e.nativeEvent.path[1].classList.add('active')//tıklanılan galeri elemanına active classı vermek için
    }
  
  const [thumbsSwiper, setThumbsSwiper] = useState(null);
  const images=[ 
    'https://swiperjs.com/demos/images/nature-1.jpg',
    'https://swiperjs.com/demos/images/nature-2.jpg',
    'https://swiperjs.com/demos/images/nature-3.jpg',
    'https://swiperjs.com/demos/images/nature-4.jpg',
    'https://swiperjs.com/demos/images/nature-5.jpg',
    'https://swiperjs.com/demos/images/nature-6.jpg',
    'https://swiperjs.com/demos/images/nature-7.jpg',
    'https://swiperjs.com/demos/images/nature-8.jpg',
    'https://swiperjs.com/demos/images/nature-9.jpg',
    'https://swiperjs.com/demos/images/nature-10.jpg'
    ]
    
    const nextSlider =()=>{
        console.log(objectAlt)
        objectUst.slideNext();
        objectAlt.slideNext();
    }
    const prevSlider=()=>{
        objectUst.slidePrev();
        objectAlt.slidePrev();
    }

        return(
            <>

            {/* Lightbox kısmı */}
            {/* onClick={()=>(document.querySelector('.lightbox').classList.add('hidden'))}  lightbox onclick */}
             <div 
                className="lightbox overflow-hidden hidden w-full h-full bg-black bg-lightbox-color absolute z-1001 top-0">
                <div className="prev" onClick={prevSlider}>
                    <InlineSVG src={collectionIcons.arrow}/>
                </div>
                <div className="next" onClick={nextSlider}>
                    <InlineSVG src={collectionIcons.arrow}/>
                </div>
                <div className="mini-slider-button close hidden transition-all" 
                    onClick={()=>{ 
                        document.querySelector('.lightbox').classList.add('hidden')
                        document.querySelector('.mini-slider-button').classList.add('hidden')
                        }}>
                    <InlineSVG src={headerIcon.close}></InlineSVG>
                </div>
                <Swiper
                    onInit={(e)=> {
                        setObjectUst(e)
                    }}
                    modules={[Thumbs]} thumbs={{ swiper: thumbsSwiper }} spaceBetween={30} slidesPerView={1} freeMode={false} className="mySwiperUst">
                    {images?.map((element, index)=>(
                        <SwiperSlide><img src={element}/></SwiperSlide>
                    ))}
                </Swiper>
                <Swiper 
                    onInit={(e)=> {
                        setObjectAlt(e)
                    }}
                    modules={[Thumbs]}
                    onSwiper={setThumbsSwiper}
                    spaceBetween={30}
                    slidesPerView={2}
                    watchSlidesProgress={true}
                    freeMode={false}
                    className="mySwiperAlt">
                    {images?.map((element, index)=>(
                        <SwiperSlide key={index}><img src={element} alt="lightbox"/></SwiperSlide>
                    ))}
                </Swiper>
            </div>

            {/* Mini slider kısmı */}
                <div className='mini-slider'>
                <Swiper spaceBetween={10} slidesPerView={2} freeMode={true}  className="mySwiper">
                    {images?.map((element, index)=>(
                        <SwiperSlide className='mini-slider-element' onClick={(e)=>hiddenThumbs(e)}><img src={element} alt="mini-slider"/></SwiperSlide>
                    ))}
                </Swiper>
                </div>
            </>
        )
    
}

export default MiniSlider;